import React, { Component } from 'react';
export default class About extends Component {

  render() {
    let resumeData = this.props.resumeData;
    if(resumeData){
      
      var description= resumeData.bio;
      var address= resumeData.address;
      
    }

    return (
      <React.Fragment>
        {/* about
              ================================================== */}
          <section id="about" className="s-about target-section">
            <div className="row">
              <div className="column large-3 tab-12">
                <img className="s-about__pic" src="images/avatars/photo.jpeg" alt="" />
              </div>
              <div className="column large-9 tab-12 s-about__content">
                <h3>About Me</h3>
                <p>
                  {description}
                </p>
                <hr />
                <div className="row s-about__content-bottom">
                  <div className="column w-1000-stack">
                    <h3>Contact Details</h3>
                    <p>
                      {resumeData.name} <br />
                      {address.street + " " + address.city} <br />
                      {address.state} <br />
                      <a href={"tel:" + resumeData.phone}>{resumeData.phone}</a> <br />
                      <a href={"mailto:" + resumeData.email}>{resumeData.email}</a>
                    </p>
                  </div>
                  <div className="columns download">
                  <p>
                     <a href={resumeData.resumeDownload} className="button"><i className="fa fa-download"></i>Download Resume</a>
                  </p>
               </div>
                </div>
              </div>
            </div> {/* end row */}
          </section> {/* end s-about */}


      </React.Fragment>
    );
  }
}